import React, {CSSProperties, useState} from 'react';
import _ from 'lodash';
import {hot} from "react-hot-loader";
import {Button, Card, Input, Navigator, Page, Radio} from "react-onsenui";
import {TransferParam} from "@web3game/typings/home";
import {getCommonAPI} from "@web3game/apis/common/getCommonAPI";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import {popPage} from "@web3game/common/utils/navigatorHelper";
import {getLang} from "@web3game/lang";
import {withAPI} from "@web3game/common/components/withAPI";
import {compose} from '@web3game/common/utils/compose';
import {inject} from 'mobx-react';
import {Str, View} from "../elements";
import {createHeaderRender} from "../Header";
import {Flex} from "../common/Flex";
import { getWeb3 } from '@web3game/apis/helpers/getWeb3';


type TransferPageProps = {
    ttBalance: number;
    tokenBalance: number;
    navigator: Navigator;
    account?: any;
    onTransfer: (p: TransferParam) => Promise<void>;
};

const styles = {
    input: {
        width: '100%',
    },
    label: {
        fontSize: 12,
    },
    button: { float: 'right' },
};

const TransferType = {
    TTI: { text: 'TTi', value: 'TTI' },
    TT: { text: 'TT', value: 'TT' },
};

export const TransferPagePure = React.memo(
    (
        {
            ttBalance,
            tokenBalance,
            account = '',
            onTransfer,
        }: TransferPageProps,
    ) => {

        const [type, setType] = useState(TransferType.TTI.value);

        const [transferTo, setTransferTo] = useState('');
        const [amount, setAmount] = useState('');
        const handleTransfer = () => onTransfer({
            toAccount: transferTo,
            amount: Number(amount),
            type,
        });

        return (
            <Page renderFixed={createHeaderRender(getLang().HOME_PAGE.TRANSFER)}>
                <Card>
                    <Str>{getLang().HOME_PAGE.TRANSFER_FROM}</Str>
                    <br />
                    <View style={styles.label}>
                        <Str>{account}</Str>
                    </View>
                    <br />
                    <View style={styles.label}>
                        <Str>TT:&nbsp;{ttBalance}&nbsp;&nbsp;TTi:&nbsp;{tokenBalance}</Str>
                    </View>
                    <br />

                    <Flex>
                        {_.map(TransferType, (transferType: any) => (
                            <label style={{ width: '100%' }}>
                                <Radio
                                    key={transferType.value}
                                    name="type"
                                    value={transferType.value}
                                    checked={transferType.value === type}
                                    modifier="material"
                                    onChange={(e: any) => setType(e.target.value)}
                                />
                                {"  "}
                                {transferType.text}
                            </label>
                        ))}
                    </Flex>

                    <br />

                    <Input
                        placeholder={getLang().HOME_PAGE.TRANSFER_TO}
                        float
                        modifier="material"
                        style={styles.input}
                        value={transferTo}
                        onChange={e => setTransferTo(e.target.value)}
                    />

                    <br />
                    <br />
                    <Input
                        placeholder={getLang().HOME_PAGE.TRANSFER_AMOUNT}
                        float
                        modifier="material"
                        style={styles.input}
                        type="number"
                        value={amount}
                        onChange={e => setAmount(e.target.value)}
                    />

                    <br />
                    <br />

                    <Button style={styles.button as CSSProperties} onClick={handleTransfer}>
                        {getLang().HOME_PAGE.TRANSFER}
                    </Button>

                    <br />
                    <br />

                </Card>
            </Page>
        );
    },
);

export const TransferPage = compose<TransferPageProps>(
    hot(module),
    inject(({accountStore}) => {
        return {
            updateDataVersion: () => accountStore.updateDataVersion(),
        };
    }),
    withAPI(
        async ({updateDataVersion}) => {
            const commonAPI = await getCommonAPI();
            const homeAPI = await getHomeAPI();
            const [account] = await commonAPI.getAccount();
            const tokenBalance = await commonAPI.getTokenBalance();
            const ttBalance = await commonAPI.getTTBalance();
            const web3 = await getWeb3();
            const onTransfer = async (params: TransferParam) => {
                // 转账地址必须合法
                if (!web3.isAddress(params.toAccount)) {
                    alert(getLang().TRANSFER.TO_ACCOUNT_INVALID);
                    return;
                }
                // 转账数量必须大于0
                if (params.amount <= 0) {
                    alert(getLang().TRANSFER.AMOUNT_GT_0);
                    return;
                }
                // TT需要有足够的余额
                if (params.type === 'TT' && params.amount > ttBalance) {
                    alert(getLang().TRANSFER.TT_NOT_ENGOUH);
                    return;
                }
                // TTi需要有足够的余额
                if (params.type === 'TTI' && params.amount > tokenBalance) {
                    alert(getLang().TRANSFER.TTI_NOT_ENGOUH);
                    return;
                }
                try {
                    await homeAPI.transfer(params);
                    updateDataVersion();
                    popPage();
                } catch (e) {
                    alert(e.message);
                }
            };
    
            return {
                account,
                tokenBalance,
                ttBalance,
                onTransfer,
            };
        },
    ),
)(TransferPagePure);

export default TransferPage;
