import React from 'react';
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import classnames from 'classnames';

export type FlexProps = {
    children?: React.ReactElement | React.ReactElement[] | string | number;
    className?: string;

    withBoxShadow?: boolean;
    fullWidth?: boolean;
    fullHeight?: boolean;
};

export const FlexPure = (
    {
        children,
        className = '',
        ...styleProps
    }: FlexProps
) => {
    const classNames = classnames(
        'flexContainer',
        styleProps,
        className,
    );

    return (
        <div className={classNames}>
            {children}
        </div>
    );
};

export const Flex = compose<FlexProps>(
    hot(module),
    React.memo,
)(FlexPure);
